@import url(reset.css);

/* common */
:root {
  /* Color */
  --main-color: #1d1d27;
  --white-color: #fff;
  --purple-color: #5d46df;
  --blue-color: #5b91fe;
  --red-dark-color: #b04078;
  --red-light-color: #fd8062;
  --grey-dark-color: #909090;
  --grey-light-color: #e0e0e0;

  /* Margin & Padding Size */
  --side-padding: 0.938rem;
  --side-margin: 0.938rem;

  /* Font Size */
  --font-huge: 3rem;
  --font-large: 2rem;
  --font-medium: 1rem;
  --font-small: 0.875rem;
  --font-micro: 0.75rem;
}

.app {
  min-height: 100vh;
  background: var(--main-color);
  color: var(--white-color);
  font-family: 'Montserrat', sans-serif;
}

.genre {
  padding: 0.375rem 0.938rem;
  border: 1px solid var(--grey-dark-color);
  border-radius: 5px;
  margin-right: 15px;
  color: var(--grey-dark-color);
  font-size: var(--font-small);
}

.page-title {
  margin: 30px 0;
  font-size: var(--font-large);
  text-transform: uppercase;
}
