@charset "UTF-8";
/* Reset CSS */
html, body {
  width: 100%;
  height: 100%;
}
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, pre, abbr, address, cite, code, del, dfn, em, img,  ins, kbd, q, samp, strong, sub, sup, var, ol, dl, dt, dd, ul, li, form, fieldset, legend, caption, input, textarea, blockquote, table, tbody, thead, tr, th, td, summary,
small, b, i, article, aside, canvas, details, figcaption, figure, footer, header, menu, nav, section, time, mark, audio, video, button {
  margin: 0;
  padding: 0;
}
article, aside, details, figcaption, figure, footer, header, menu, nav, section, video {
  display: block;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: 100%;
}
table {
  border-collapse: collapse;
}
caption, th {
  text-align: left;
}
address, caption, cite, code, em, strong, th {
  font-style: normal;
  font-weight: normal;
}
ul, ol {
  list-style: none;
}
img {
  max-width: 100%;
}
img, fieldset, button {
  border: none;
}
img, input, textarea, label, button {
  vertical-align: middle;
}
q:after, q::before {
  content: '';
}
abbr {
  border: none;
}
hr {
  display: none;
}
a {
  text-decoration: none;
  color: #666;
}
#skipNavi, .blind {
  visibility: hidden;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 0;
  font-size: 0;
  line-height: 0;
  text-indent: -9999px;
}    