@import url(../App.css);

/* Style Visual */
.visual {
  width: 100%;
  height: 50vh;
  padding: 200px 0 0 30px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(23, 38, 90, 0.8)), url(../img/bg_marvel.png);
  background-repeat: no-repeat;
  background-size: cover;
  box-sizing: border-box;
}

.visual h2 {
  font-size: var(--font-huge);
  margin-bottom: 15px;
}
.visual-text {
  width: 60%;
  margin-bottom: 15px;
  color: var(--grey-dark-color);
  line-height: 1.3;
  word-break: break-all;
}
.star {
  font-size: var(--font-small);
  margin-bottom: 15px;
  color: rgb(255, 208, 0);
}
.star i {
  margin-right: 2px;
}

@media screen and (max-width:960px) {
  .visual h2 {
    font-size: var(--font-large);
  }
  .visual-text {
    width: 90%;
  }
}