@import url(../App.css);

.movie-box {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 200px;
  margin-bottom: 15px;
}
.movie-box:hover img,
.movie-box:focus img {
  opacity: 1;
}
.movie-box__poster {
  opacity: .5;
  border-radius: 5px;
  transition: all .3s ease 0s;
}
.movie-box__title {
  margin: 10px 0;
}
.movie-box__des {
  display: flex;
  justify-content: space-between;
  color: var(--grey-dark-color);
  font-size: var(--font-micro);
}

@media screen and (max-width: 760px) {
  .movie-box {
    width: 46%;
  }
}